  <v-form ref="form" @submit.prevent>
    <v-container>
      <v-subheader>Instituição</v-subheader>
      <v-row align="center">
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="instituicao.cdUsuario"
            label="Id"
            outlined
            disabled
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="12" sm="6">
          <v-text-field
            v-model="instituicao.nome"
            :counter="50"
            label="Nome"
            placeholder="Nome da Instituição"
            outlined
            required
          ></v-text-field>
        </v-col>
        <v-col class="d-flex" cols="12">
          <v-text-field
            v-model="instituicao.cnpj"
            hint="00.000.000/0000-00"
            label="CPF"
            placeholder="CNPJ da Instituição"
            outlined
            required
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center">
        <v-col class="d-flex" cols="6">
          <v-text-field
            v-model="instituicao.login"
            :counter="50"
            label="Login"
            placeholder="Login da Instituição"
            outlined
            required
          ></v-text-field>
        </v-col>
        <v-col class="d-flex" cols="6">
          <v-text-field
            v-model="instituicao.senha"
            :counter="50"
            :type="fldPassword ? 'text' : 'password'"
            :append-icon="fldPassword ? 'mdi-eye' : 'mdi-eye-off'"
            hint="Pelo menos 3 caracteres"
            name="fldSenha"
            label="Senha"
            placeholder="Senha da Instituição"
            outlined
            required
            @click:append="fldPassword = !fldPassword"
          ></v-text-field>
        </v-col>
      </v-row>
      <!--<v-row>
        <v-col class="d-flex" cols="12" sm="6">
          <v-text-field
            v-model="instituicao.rua"
            :counter="50"
            label="Rua"
            placeholder="Nome da Rua da Instituição"
            outlined
            required
          ></v-text-field>
        </v-col>
        <v-col class="d-flex" cols="12" sm="4">
          <v-combobox
            :items="bairros"
            :hint="`${instituicao.bairro.cidade.nome + ' - ' + instituicao.bairro.cidade.uf.sigla}`"
            v-model="instituicao.bairro"
            item-text="nome"
            item-value="id"
            label="Bairro"
            placeholder="Escolha o Bairro"
            return-object
            outlined
            required
          ></v-combobox>
        </v-col>
        <v-col class="d-flex" cols="12" sm="2">
          <v-text-field
            v-model="instituicao.numCasa"
