            placeholder="Nº do Imóvel da Instituição"
            type="number"
            outlined
          ></v-text-field>
        </v-col>
      </v-row>-->
      <v-btn color="success" class="" @click="save">Salvar</v-btn>
    </v-container>
  </v-form>
</template>

<script>
import DataService from "../service/InstituicaoDataService";
import BairroDataService from "../service/BairroDataService";

export default {
  name: "Instituição",
  data() {
      return {
          instituicao: {cdUsuario:0,nome:'',login:'',senha:'',cnpj:0,rua:'',numCasa:'',bairro:{id:0,nome:'',cidade:{id:0,nome:'',uf:{id:0,sigla:'',nome:''}}}},
          fldPassword: false,
          bairros: [],
          rules: {
            required: value => !!value || 'Required.',
          },
      };
  },
  computed: {
      id() {
          return this.$route.params.id;
      }
  },
  methods: {
      refresh() {
          if(this.id > 0){
              DataService.retrieve(this.id)
                  .then(response => {
                  this.instituicao = response.data
              });
          }
          BairroDataService.retrieveAll()
              .then(response => {
              this.bairros = response.data
          });
      },
      save(e) {
          e.preventDefault();
          if(this.id > 0){
              DataService.update(this.id, this.instituicao, {
                  id: this.id,
                  instituicao: this.instituicao
              }).then(() => {
                  this.$router.push('/instituicoes')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          } else {
              console.log(this.instituicao);
              DataService.create(this.instituicao, {
                  instituicao: this.instituicao
              }).then(() => {
                  this.$router.push('/instituicoes')
              }).catch(e => {
                  console.log(e)
                  if(e.response){
                    this.$store.dispatch('setSnackbar', {
                      showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
                    });
                  }
              });
          }
      }
  },
  created() {
      this.refresh()
  }
};
</script>

