import axios from "axios";

const API_URL = "http://localhost:8080/";
const ENDPOINT = `${API_URL}/bairros`;

class BairroDataService {
    retrieveAll() {
        return axios.get(`${ENDPOINT}`);
    }
    retrieve(id) {
        return axios.get(`${ENDPOINT}/${id}`);
    }
    remove(id) {
        return axios.delete(`${ENDPOINT}/${id}`);
    }
    update(id, obj) {
        return axios.put(`${ENDPOINT}/${id}`, obj);
    }
    create(obj) {
        return axios.post(`${ENDPOINT}`, obj);
    }
}

export default new BairroDataService();